.blog-article-cover-top {
    @include desktop {
    display: flex;
    // gap: 121px;
    justify-content: center;
    }
}

/* ------blog-article-middle----------- */
.blog-article-middle-title {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.18;
     color: #FFFFFF;
    @include desktop {
    margin-top: 48px;
    font-size: 24px;
    line-height: 1.16;
    color: #86E543;
    max-width: 600px;
    }
    @include tablet {
            font-weight: 400;
            font-size: 24px;
            line-height: 1.16;
            color: #86E543;
        }
}

.blog-article-middle-list {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.18;
    color: #FFFFFF;
    @include desktop {
    font-size: 24px;
    line-height: 1.16;
    color: #86E543;
    margin-left: 30px;
    }
    @include tablet {
    font-weight: 400;
    font-size: 24px;
    line-height: 1.16;
    color: #86E543;
    margin-bottom: 24px;
        }
}

/* ------blog-article-bottom----------- */
.blog-article-container-bottom {
    padding-bottom: 48px;
    @include desktop {
        padding-bottom: 120px;
    }
}

.blog-article-bottom-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.18;
    color: #FFFFFF;
    max-width: 288px;
    @include desktop {
    font-size: 24px;
    line-height: 1.16;
    margin-top: 24px;
    max-width: 1200px;
    }
    @include tablet {
font-weight: 400;
font-size: 24px;
line-height: 1.16;
color: #FFFFFF;
max-width: 688px;
    }
}

.blog-article-bottom-paragraph{
    margin-bottom: 24px;
    @include desktop{
        &:not(:last-child){
        margin-bottom: 24px;
    }

    }
    
}
// ---------------TAB--------------
.blog-article-tab-cover-top{
    @include tablet {
        display: flex;
        align-items: flex-end;
        margin-bottom: 16px;
    }
}
.blog-article-tab-title-top {
    @media screen and (max-width:768px){
display: none;
    }
        @include desktop {
            display: none;
        }
    margin-left: 40px;

}
.blog-article-tab-title-top-first {
    margin-bottom: 32px;
font-weight: 600;
    font-size: 24px;
    line-height: 100%;
    color: #86E543;
    max-width: 248px;
}
.blog-article-tab-title-top-second{
font-weight: 600;
    font-size: 24px;
    line-height: 100%;
    color: #8D9289;
    max-width: 248px;
}