.order-mob-section-title{
    font-weight: 600;
    font-size: 32px;
    line-height: 1.18;
    color: #FFFFFF;
    max-width: 287px;
    margin-bottom: 16px;
    @include tablet{
        font-size: 54px;
        max-width: 692px;
    }
}
.order-mob-main-img{
    margin-bottom: 16px;
}
.order-mob-price-without-discount {
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    text-decoration-line: line-through;

    color: #8D9289;

    @include tablet{
        font-size: 24px;
    }

}

.order-mob-price-with-discount {
    font-weight: 600;
    font-size: 32px;
    line-height: 100%;

    color: #86E543;

    @include tablet {
            font-size: 48px;
        }
}

.order-mob-prices{
    display: flex;
    gap: 60px;
    align-items: flex-start;
    margin-bottom: 16px;
    @include tablet{
        flex-wrap: wrap;
        align-content: center;
    }
}

.order-mob-main-img{
margin-bottom: 16px;
}

.order-mob-container{
    @include tablet{
        display: flex;
        gap: 40px;
    }
}

.order-mob-hide{
    @media screen and(min-width: 1279px){
        display:none;
    }
}

.order-main-img,
.order-section-title,
.order-price-without-discount,
.order-price-with-discount{
    @media screen and(max-width:1279px){
        display: none;
    }
}


