.order-infiormation {
    display: flex;
    gap: 121px;
    margin-bottom: 32px;
    @include tablet{
        margin-bottom: 48px;
    }
}

.order-title{
    font-weight: 600;
    font-size: 24px;
    line-height: 1.16;
    color: #FFFFFF;
}

.order-section-title{

    font-weight: 600;
        font-size: 32px;
        line-height: 1.18;
        color: #FFFFFF;
        max-width: 287px;
    @include tablet{
        max-width: 692px;

    }
}

.order-title,
.order-section-title {
    @include tablet{
    font-size: 54px;
        line-height: 1.17;
    }
}

.order-title {
    margin-left: 100px;
    margin-bottom: 75px;
}

.order-section-title {
    margin-bottom: 32px;
}

.order-price-without-discount {
    font-weight: 600;
    font-size: 24px;
    line-height: 100%;
    text-decoration-line: line-through;

    color: #8D9289;
}

.order-price-with-discount {
    font-weight: 600;
    font-size: 48px;
    line-height: 100%;

    color: #86E543;
}

.order-prices {
    display: flex;
    gap: 40px;
    align-items: center;
    margin-bottom: 40px;
    @media screen and(max-width: 1279px){
        margin-bottom: 0;
    }
