/* --------------Order Form------------- */

.order-form-title {
    font-weight: 600;
    font-size: 24px;
    line-height: 1.16;

    color: #86E543;
    margin-bottom: 16px;

    @include desktop {
        font-size: 36px;
        margin-bottom: 48px;
    }
}

.order-form-part-one,
.order-form-part-two {
    @include tablet {
        min-width: 590px;
    }

    @include desktop {
        // display: flex;
        // flex-direction: column;
    }

}

