.order-form {
    max-width: 480px;

    @include tablet{
        max-width: 523px;
    }

    @include desktop {
        display: flex;
        align-items: baseline;
        gap: 20px;
    }
}

.order-label {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.11;

    text-transform: capitalize;

    color: #FFFFFF;

}

.form-item {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid rgba(134, 229, 67, 1);
    width: 100%;
    height: 40px;
    margin-bottom: 24px;
    outline: transparent;
    color: #fcfcfc;
}

.order-form-thumb {
    position: relative;
}

.order-label.input {
    position: absolute;
    left: 0;
    top: 10px;
}

.form-item:focus+.order-label.input {
    transform: translateY(-100%);
}

.order-text-area {
    background-color: transparent;
    border: 1px solid rgba(134, 229, 67, 1);
    width: 100%;
    height: 95px;
    margin-top: 4px;
    resize: none;
    outline: transparent;
    font-family: inherit;
    padding: 15px;
    color: #fcfcfc;

    @include tablet {
        height: 256px;
    }
}

.order-dropdown {
    border-bottom: 1px solid rgba(134, 229, 67, 1);
    font-family: inherit;
    font-size: 18px;
    font-weight: 400;
    color: #FFFFFF;
    margin-bottom: 22px;
    height: 40px;
    width: 100%;
}

.btn-buy {
    font-family: inherit;
    font-weight: 700;
    line-height: 1.18;
    font-size: 16px;
    text-transform: uppercase;
    color: #01383A;
    background-color: #86E543;
    border-radius: 16px;
    cursor: pointer;
    border: none;

    padding: 16px 42px;
    margin-bottom: 16px;

    @include tablet {
        font-size: 32px;
        padding: 16px 64px;
    }
    @include desktop{
        margin-bottom: 0;
    }
}

.btn-buy:hover {
    box-shadow: 0px 8px 20px rgba(134, 229, 67, 0.5), 0px 4px 10px rgba(255, 255, 255, 0.1);
}

.btn-add {
    font-family: inherit;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.18;
    text-transform: uppercase;
    color: #86E543;
    background-color: #01383A;
    border-radius: 16px;
    cursor: pointer;
    border: 1px solid #86E543;

    padding: 16px 35px;

    @include tablet {
        font-size: 32px;
        padding: 16px 51px;
    }
}

.btn-add:hover {
    filter: drop-shadow(0px 4px 10px rgba(134, 229, 67, 0.1)) drop-shadow(0px 4px 20px rgba(134, 229, 67, 0.4));
}

.order-btns>button {
    display: block;
    margin-left: auto;
    margin-right: auto;
    @include desktop{
        margin-left: 0;
        margin-right: 0;
    }
}

.order-btns {
    margin-top: 24px;

    @include desktop {
        display: flex;
        justify-content: center;
        gap: 20px;

        margin-top: 48px;
    }
}