.banner {
        display: block;
        margin: 0 auto;
        max-width: 1280px;
}
.swiper-wrapper {
        margin-bottom: 32px;
}
.swiper-slide {
        position: relative;
}
.banner__img img {
        margin: 0 auto;
        height: 507px;
        object-fit: cover;

}

.banner__one {
        position: absolute;
        left: 24px;
        top: 405px;
        max-width: 257px;
        font-weight: 600;
        font-size: 24px;
        line-height: 1.17;
        text-align: left;
        
        background:  linear-gradient(270deg, #1EDDE0 13.26%, #8BDA41 51.01%, #EF001B 107.89%);
        background-clip: text;
        color: transparent;

        @include tablet {
                left: 41px;
                top: 352px;
                max-width: 596px;
                font-size: 56px;
                line-height: 1.18;
        }
        @include desktop {
                right: 0;
                left: 0;
                top: 326px;
                bottom: 0;
                margin: auto;
        }
}

