.swiper-pagination {

}
.swiper {
        & .swiper-pagination {
                position: relative;
                bottom: 0;
        }
        & .swiper-pagination-bullet {
                margin: 0;
                width: 16px;
                height: 16px;
                opacity: 1;
                background: #fff;

        }
        & .swiper-pagination-bullet-active {
                width: 40px;
                height: 16px;
                opacity: 1;
                background: #fff;
                border-radius: 30px;

        }
}